.social {
  @include flex-v-center;

  &__item {
    border-radius: 100%;
    width: 32px;
    height: 32px;
    background-color: #d2d2d2;
    transition: background-color 500ms ease-in-out;

    &:not(:last-child) {
      margin-right: 10px;
    }

    &:hover {
      background-color: orange;
    }
  }

  &__link {
    @include bg-cover;

    display: block;
    width: 100%;
    height: 100%;
    background-size: 17px;

    &--in {
      @include image-set("../img/social/linkedin");
    }

    &--tw {
      @include image-set("../img/social/twitter");
    }

    &--pt {
      @include image-set("../img/social/pinterest");
    }

    &--gp {
      @include image-set("../img/social/google-plus");
    }

    &--rss {
      @include image-set("../img/social/rss");
    }
  }
}
