.header {
  &__links {
    border-bottom: 1px solid var(--main-color);
    padding: 9px 0;
    background-color: var(--light-color);
  }

  &__links-wrapper {
    @include flex-all-sb;

    position: relative;
  }

  &__links-list {
    @include flex-v-center;
  }

  &__links-item {
    position: relative;

    &:not(:last-child) {
      margin-right: 12px;

      &::after {
        content: "";
        position: absolute;
        right: -8px;
        top: 3px;
        width: 1px;
        height: 12px;
        background-color: var(--main-color);
      }
    }
  }

  &__links-link {
    font-size: 12px;
    letter-spacing: 0.3px;
    color: var(--main-color);

    &:hover {
      text-decoration: underline;
    }
  }

  &__links-btn {
    position: absolute;
    right: 0;
    bottom: -35px;
    border-radius: 0 0 var(--border-radius) var(--border-radius);
    width: 30px;
    height: 26px;
    background-color: var(--main-color);
    transform: translateX(-50%);
    transition: background-color 500ms ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 11px;
      height: 1px;
      background-color: var(--light-color);
      transform: translate(-50%, -50%);
    }

    &:hover {
      background-color: #622d3e;
    }
  }

  &__logo {
    padding: 35px 0 28px;
    background-color: #ebebeb;
  }

  &__logo-wrapper {
    @include flex-all-sb;
  }

  &__logo-content {
    @include flex-v-center;
  }

  &__logo-title {
    margin-right: 18px;
    font-weight: 400;
    font-size: 26px;
    text-transform: uppercase;
    color: var(--dark-color);

    span {
      font-size: 36px;
    }
  }

  &__logo-text {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: -10px;
      top: 3px;
      width: 1px;
      height: 25px;
      background-color: currentcolor;
    }
  }

  &__navigation-wrapper {
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      bottom: -80px;
      width: 20px;
      height: 130px;
      background-color: #ebebeb;
    }

    &::before {
      left: -5px;
    }

    &::after {
      right: -5px;
    }
  }
}
