html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  &--bigger {
    max-width: var(--container-bigger-width);
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.text-center {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.text {
  font-size: 12px;
  color: var(--gray-color);
}

.title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--dark-color);

  &--pseudo-elements {
    @include flex-v-center;

    &::before,
    &::after {
      content: "";
      flex-grow: 1;
      height: 5px;
      background-color: #ebebeb;
    }

    &::before {
      margin-right: 7px;
    }

    &::after {
      margin-left: 7px;
    }
  }
}

.main-title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
}

.footer-contacts-text {
  font-size: 12px;
  color: #999;
}
