.cards {
  padding: 36px 0 0;

  &__title {
    margin-bottom: 38px;
  }

  &__wrapper {
    @include flex;

    flex-wrap: wrap;
  }

  &__card {
    --elements: 3;
    --margin: 37px;

    margin-bottom: 41px;

    &:not(:nth-child(3n)) {
      margin-right: var(--margin);
    }
  }

  &__image {
    flex-shrink: 0;
    margin-right: 10px;
    width: 64px;
    height: 64px;
    text-align: center;
    background-color: #353535;
  }

  &__card-title {
    margin-bottom: 12px;
  }
}
