.about {
  padding: 7px 0 50px;

  &__cards {
    margin-bottom: 25px;
  }

  &__wrapper {
    @include flex;

    flex-wrap: wrap;
  }

  &__card {
    --elements: 2;
    --margin: 20px;

    flex-direction: column;
    margin-bottom: 20px;

    &:not(:nth-child(2n)) {
      margin-right: var(--margin);
    }
  }

  &__title {
    margin-bottom: 33px;
  }

  &__card-content {
    @include flex;

    &--column {
      flex-direction: column;
    }
  }

  &__card-image {
    flex-shrink: 0;
    margin-right: 18px;
    border-radius: 100%;
    width: 128px;
    height: 128px;
    text-align: center;
    background-color: var(--dark-color);
    transform: translateY(-8px);
  }

  &__card-title {
    margin-bottom: 3px;
    text-transform: capitalize;

    &--margin-bottom {
      margin-bottom: 12px;
    }
  }

  &__card-descr {
    &--margin-bottom {
      margin-bottom: 13px;
    }
  }

  &__card-link {
    font-size: 13px;
    color: var(--main-color);
  }

  &__card-wrapper {
    position: relative;
    margin-bottom: 5px;
    padding-left: 66px;

    &::before {
      @include image-set("../img/about/about-icon");

      content: "";
      position: absolute;
      left: 2px;
      top: -2px;
      width: 53px;
      height: 44px;
    }
  }

  &__contacts-content {
    @include flex-all-sb;

    border-radius: var(--border-radius);
    padding: 22px 31px;
    background-color: var(--main-color);
  }

  &__contacts-title {
    color: var(--light-color);
  }

  &__contacts-descr {
    font-size: 18px;
    color: var(--light-color);
  }

  &__contacts-link {
    flex-shrink: 0;
    border-radius: var(--border-radius);
    padding: 14px 10px;
    font-size: 26px;
    text-align: center;
    text-transform: uppercase;
    color: var(--main-color);
    background-color: var(--light-color);
    transition: background-color 500ms ease-in-out;

    &:hover {
      background-color: #ccc;
    }
  }
}
