.footer {
  border-top: 5px solid #d2d2d2;
  background-color: #ebebeb;

  &__content {
    border-bottom: 1px solid #bcbcbc;
    padding: 40px 0;
  }

  &__content-wrapper {
    @include flex;
  }

  &__content-block {
    &:not(:last-child) {
      margin-right: 37px;
    }

    &--max-width {
      max-width: 455px;
    }

    &--offset {
      margin-left: auto;
    }
  }

  &__content-card {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__content-title {
    margin-bottom: 16px;
    text-transform: capitalize;
  }

  &__card-title {
    margin-bottom: 14px;
    font-size: 16px;
  }

  &__image-wrapper {
    flex-shrink: 0;
    margin-right: 20px;
    border: 1px solid #d2d2d2;
    padding: 4px;
    width: 105px;
    height: 105px;
  }

  &__image {
    width: 100%;
    height: 100%;
    background-color: var(--dark-color);
  }

  &__address {
    margin-bottom: 16px;
  }

  &__address-item {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__contacts {
    margin-bottom: 20px;
  }

  &__time {
  }

  &__time-title {
    margin: 0;
    margin-bottom: 5px;
  }

  &__time-item {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__content-form {
    flex-direction: column;
    align-items: flex-start;
    width: 226px;
    height: auto;
  }

  &__content-input {
    margin-bottom: 10px;
    border-radius: var(--border-radius);
    width: 100%;
    height: 31px;

    &--textarea {
      height: 110px;
      resize: none;
    }
  }

  &__content-btn {
    border-radius: var(--border-radius);
    padding: 9px 17px;
  }

  &__rights {
    border-top: 1px solid var(--light-color);
    padding: 24px 0;
  }

  &__rights-wrapper {
    @include flex-all-sb;
  }
}
