// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Georgia", sans-serif;
  --content-width: 900px;
  --content-bigger-width: 960px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-bigger-width: calc(var(--content-bigger-width) + (var(--container-offset) * 2));

  // colors
  --main-color: #97455f;
  --light-color: #fff;
  --dark-color: #333;
  --black-color: #383838;
  --gray-color: #999;

  // border
  --border-radius: 6px;
}
