.card {
  @include flex;

  width: calc((100% - (var(--margin) * (var(--elements) - 1))) / var(--elements));

  &__title {
    margin: 0;
    text-transform: capitalize;
    color: var(--main-color);
  }

  &__descr {
    margin: 0;
    font-size: 13px;
    line-height: 17px;
    letter-spacing: 0.5px;
    color: #999;
  }
}
