.hero {
  padding: 30px 0 45px;

  &__image {
    position: relative;
    width: 100%;
    height: 360px;
    background-color: var(--dark-color);
  }

  &__image-text {
    font-size: 24px;
  }

  &__image-block {
    position: absolute;
    right: 11px;
    bottom: 28px;
    padding: 9px 28px;
    width: 455px;
    height: 115px;
    background-color: #adadad;
  }

  &__image-title {
    margin-bottom: 15px;
    color: var(--black-color);
  }

  &__image-descr {
    margin: 0;
    font-size: 16px;
    color: var(--black-color);
  }

  &__image-dots {
    @include flex-v-center;

    position: absolute;
    left: 50%;
    bottom: -35px;
    height: 15px;
    transform: translateX(-50%);
  }

  &__image-dot {
    border: 3px solid var(--dark-color);
    border-radius: 100%;
    width: 16px;
    height: 16px;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  &__image-dot--checked {
    background-color: var(--dark-color);
  }
}
